import Header from '../../components/header/header'
import Footer from '../../components/footer/footer'
import { useRef } from 'react'
import { useContext } from 'react';
import context from '../../lang/lang';
import ParticlesBg from 'particles-bg'
import { Context as LanguageContext } from '../../Context/Language';
import Image from '../../images/93809a2de8_1621412188.jpg'
import { Link } from 'react-router-dom';
import Logo from '../../images/logo-circle.png'


function PageStaticOne() {
    const tabOne = useRef()
    const tabTwo = useRef()
    const tabThree = useRef()
    const { lang } = useContext(LanguageContext);

    return (
        <>
            <ParticlesBg color="#307ec7" type="cobweb" num={50} bg={true} />
            <div className="container-pages">
                <Header />
                <div className="mainsss">
                <div className='page_main'>
                    <div class="row">
                        <div class="col-lg-10 col-md-12">
                            <h3>{context[lang].centre.seventh}</h3>
                            <div class="tab">
                                <button class="tablinks" onClick={() => {
                                    tabTwo.current.classList.remove("opened")
                                    tabThree.current.classList.remove("opened")
                                    tabOne.current.classList.add("opened")
                                }} id="defaultOpen">Bo'lim haqida</button>
                                <button class="tablinks" onClick={() => {
                                    tabOne.current.classList.remove("opened")
                                    tabThree.current.classList.remove("opened")
                                    tabTwo.current.classList.add("opened")
                                }} id="defaultOpen">Bizning
                                    maqsadimiz</button>
                                <button class="tablinks" onClick={() => {
                                    tabOne.current.classList.remove("opened")
                                    tabTwo.current.classList.remove("opened")
                                    tabThree.current.classList.add("opened")
                                }} id="defaultOpen">Bizning
                                    vazifamiz</button>
                            </div>
                            <div id="About" ref={tabOne} class="tabcontent opened">
                                <h3>Bo'lim haqida</h3>
                                <p>Toshkent Kimyo-texnologiya institutining oquv bolimni asosiy  vazifalariga oquv jarayonini tashkil qilish, Davlat talim standartlari, namunaviy oquv rejalar va namunaviy oquv dasturlarini va boshqa meyoriy xujjatlarni ishlab chiqish, oquv jarayonining grafigini, oquv rejalarni tuzish, mashgulotlar jadvalini tuzishga rahbarlik qilish va uni fakultetlarda tuzilishini nazoratga olish, oquv ishlar xajmini aniqlab fakultet va kafedralar boyicha shtat jadvallarini hamda soatbay yuklamalarni tuzish, oquv ishlarini bajarilishini tekshirish, institut yillik hisobotini tuzish, oquv jarayoniga reyting tizimi va yangi pedagogik texnologiyalarni joriy qilish, Davlat Attestatsiyalarni tashkil qilish va uni otkazish etish, kvalifikatsion amaliyotlarni tashkil etish, semestrlar yakunidan talabalarni ozlashtirishini umumlashtirish, institut auditoriya fondidan foydalanishni tekshirish, mashgulotlarni fan dasturlariga mos ravishda toliq otishi va darslarga qatnashganligini tekshirish boyicha dispetcherlik xizmatini tashkil qilish bilan bogliq bolgan ishlarni oz ichiga oladi.</p>
                            </div>

                            <div id="Functions" ref={tabTwo} class="tabcontent">
                                <h3>Bizning maqsadimiz</h3>
                                <p> Ozbekiston Respublikasi kadrlar tayyorlash milliy dasturini joriy etishni uchinchi bosqichida oquv bolimi  yuqori malakali mutaxassislarni tayyorlash vazifalarini bajarishida quyidagilarga asosiy etibor qaratadi:
                                    <br />
                                    Talabalarning bilimlarini reyting tizimi asosida nazorat qilishda, ularning mustaqil ishlarini oquv jarayoniga togri tadbiq qilish;
                                    Oquv jarayonini tasdiqlangan oquv va normativhuquqiy xujjatlar asosida tashkil qilish;
                                    Professoroqituvchi va talabalarga yuqori darajada ishonch va etibor bilan ijobiy muhitni yaratish;
                                    Iqtidorli talabalarni tanlab, ularni maqsadli, kasbiy mahoratlarini oshirish;
                                    Yangi pedagogik texnologiyalarni joriy etish, professoroqituvchilarning pedagogik mahoratlarini oshirish, ishlab chiqarish korxonalarining yetuk mutaxassislarni oquv jarayoniga jalb etish;
                                    Ishlab chiqarish bilan oliy talim integratsiyasini yaxshilash;
                                    Oquvmetodik adabi         yotlar bazasini kengaytirish;
                                    Oquv jarayonini boshqarish va uni tashkil etishda axborot texnika  komunikatsiyalariga yaqindan yondashish.
                                    Oquv bolimini asosiy ish rejalari quyidagilardan iborat
                                    <br />
                                    Oquv jarayoni grafigini tuzish (mart-aprel)
                                    Yonalish va mutaxassisliklar boyicha oquv rejalarini tuzish (mart-aprel)
                                    Yangi oquv yili oquv jarayonini rejalashtirish (aprel-avgust)
                                    Oquv jarayonini tashkil etish va nazorat qilish (oquv yili davomida)
                                    Talabalar kontingenti monitoringini amalga oshirish (oquv yili davomida)
                                    Kvalifikatsion amaliyotlarni tashkil etish va nazorat qilish (oquv yili davomida)
                                    Davlat Attestatsiya yakunlarini otkazilishini nazorat qilish (dekabr-yanvar, may-iyun).</p>
                            </div>


                            <div id="Management" ref={tabThree} class="tabcontent">
                                <h3>Bizning vazifamiz</h3>
                                <p>Institutda oqitiladigan barcha bakalavriyat talim yonalishlari va magistratura mutaxassisliklari boyicha Davlat talim standartlari tuzilib Davlat Standarti tasdiqidan otkazildi, Namunaviy fan dasturlari   ornatilgan tartibga kora tasdiqlandi. Tasdiqlangan meyoriy xujjatlar elektron nusxaga otkazildi va ular bilan turdosh OTM taminlanmoqda.

                                    Tayanch xisoblangan bakalavr talim yonalishlari va magistratura mutaxassisliklarining asosiy fanlari boyicha vazirlikning talablariga kora Oquv uslubiy majmualar yaratilmoqda;

                                    Institutda Davlat Test Markazi tomonidan otkaziladigan attestatsiyaga puxta tayyorgarlik korish maqsadida, talabalarning qoldiq bilimlarini oshirishga yonaltirilgan barcha fanlardan testlar sinovlari otkazilmoqda;
                                    <br />
                                    Toshkent Kimyo-texnologiya institutiga turdosh bolgan OTMlari bilan Elektron oquv-uslubiy majmualar va talimni modernizatsiyalashning ustivor yonalishlari deb nomlangan ilmiy-uslubiy semenarni otkazilishi may oyining oxiriga rejalashtirilgan, bu haqida Oliy talim vazirligining modemnomasi yuboriladi.

                                    Oquv uslubiy bolim Respublikamizdagi quyidagi Oliy talim muassasalari bilan oquv va oquv-uslubiy ishlar boyicha yaqindan ijodiy ishlar olib bormoqda:

                                    OzMU, TDTU, QQDU, TAQI, TTESI, TIMI, And.MII, FarPI, SamDU, UrgDU, BuxMTI, QarDU, NavDKI, Qarshi MII, JizPI, ToshFarmI, NamMTI va boshqalar.
                                </p>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-10">
                            <div class="teachers justify-content-start">
                                <div class="wrapper-personal">
                                    <div class="photo-personal">
                                        <img class="images-personal" src={Image} alt="About us" />
                                        <div class="photo__large">
                                            <div class="bg-text">
                                                <span class="degree">Bo'lim boshlig'i</span>
                                                <span class="info"> Xabibullayev Rashid Azamatovich

                                                    Xizmat telefoni: 71 2447934;

                                                    E-mail: rashidxabibullayev@gmail.com.</span>
                                            </div>
                                        </div>
                                    </div>
                                    <h6 class="fullname-personal">Xabibullayev Rashid Azamatovich</h6>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div className="navbar-side">
                    <ul className="nav-ull">
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Bo`limlar</p></li>
                        <li className="nav-ul-li active"><a href="">Oquv-uslubiy bolimi</a></li>
                        <Link to={'/page/static/get/8'}>
                        <li className="nav-ul-li"><a href="">Marketing va talabalar amaliyoti bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/9'}>
                        <li className="nav-ul-li"><a href="">Jismoniy va yuridik shaxslarning murojaatlari bilan ishlash, nazorat va monitoring bo'limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/15'}>
                        <li className="nav-ul-li"><a href="">Xalqaro aloqalar bo`limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/13'}>
                        <li className="nav-ul-li"><a href="">Talim sifatini nazorat qilish bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/7'}>
                        <li className="nav-ul-li"><a href="">Magistratura bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/4'}>
                        <li className="nav-ul-li"><a href="">Ilmiy innovatsion ishlanmalarni tijoratlashtirish bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/6'}>
                        <li className="nav-ul-li"><a href="">Kadrlar bo'limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/12'}>
                        <li className="nav-ul-li"><a href="">Sirtqi bo'lim</a></li>
                        </Link>
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Markazlar</p></li>
                        <Link to={'/page/static/get/11'}>
                        <li className="nav-ul-li"><a href="">Raqamli ta'lim texnologiyalari markazi</a></li>
                        </Link>
                        <Link to={'/page/static/get/14'}>
                        <li className="nav-ul-li"><a href="">TKTI huzuridagi Pedagogik kadrlarni qayta tayyorlash va malakasini oshirish tarmoq markazi</a></li>
                        </Link>
                        <Link to={'/page/static/get/5'}>
                        <li className="nav-ul-li"><a href="">Innovasion markazi</a></li>
                        </Link>
                    </ul>
                    </div>
                    </div>
            </div>
            <Footer />
        </>
    );
}

export default PageStaticOne;
