import Header from '../../components/header/header'
import Footer from '../../components/footer/footer'
import { useContext } from 'react';
import context from '../../lang/lang';
import { Context as LanguageContext } from '../../Context/Language';
import Image from '../../images/7ba0791499_1541489244.jpg'
import { useRef } from 'react';
import { Link } from 'react-router-dom';
import Logo from '../../images/logo-circle.png'


function PageStaticOne() {
    const tabOne = useRef()
    const tabTwo = useRef()
    const tabThree = useRef()
    const { lang } = useContext(LanguageContext);
    return (
        <>
            <div className="container-pages">
                <Header />
                <div className="mainsss">
                <div className='page_main page_4'>
                    <div className="container-fluid">
                        <div class="row">
                            <div class="col-lg-10 col-md-12">
                                <h3>{context[lang].centre.nineth}</h3>
                                <div class="tab">
                                    <button class="tablinks" onClick={() => {
                                        tabTwo.current.classList.remove("opened")
                                        tabThree.current.classList.remove("opened")
                                        tabOne.current.classList.add("opened")
                                    }} id="defaultOpen">Bo'lim haqida</button>
                                    <button class="tablinks" onClick={() => {
                                        tabOne.current.classList.remove("opened")
                                        tabThree.current.classList.remove("opened")
                                        tabTwo.current.classList.add("opened")
                                    }} id="defaultOpen">Bizning
                                        maqsadimiz</button>
                                    <button class="tablinks" onClick={() => {
                                        tabOne.current.classList.remove("opened")
                                        tabTwo.current.classList.remove("opened")
                                        tabThree.current.classList.add("opened")
                                    }} id="defaultOpen">Bizning
                                        vazifamiz</button>
                                </div>
                                <div id="About" ref={tabOne} class="tabcontent opened">
                                    <h3>Bo'lim haqida</h3>
                                    <p>Ilmiy  innovatsion  ishlanmalarni  tijoratlashtirish  bolimi (IIITB) Ozbekiston Respublikasi Prezidentining 2018 yil 7 maydagi Iqtisodiyot tarmoqlari va soxalariga innivatsiyalarni joriy etish mexanizmlarini takomillashtirish boyicha qoshimcha chora tadbirlar togrisidagi PQ-3698-sonli qaroriga asosan 2018 yil 1 avgustdan tashkil etildi.<br />
                                        Ozbekiston Respublikasi Prezidentining 2018 yil 14 iyuldagi Ilmiy va ilmiy-texnikaviy faoliyat natijalarini tijoratlashtirish samaradorligini oshirish boyicha qoshimcha chora-tadbirlar togrisidagi PQ-3855-sonli qaroriga asosan institutda innivatsion jamgarma ochildi. Innivatsion jamgarmaga tushgan mablaglarni togri taqsimlanishini olib borish uchun Innovatsion Kengash tuzildi.
                                        <br />
                                        Ilmiy  innovatsion  ishlanmalarni  tijoratlashtirish  bolimi innovatsion ishlanmalardan tushgan mablaglar, tolov shartnoma xisobidan tushgan mablagning 5% (oylik, stipendiya va boshqa xarajatlarni qoplangandan song) xamda xomiylar xisobidan  oz faoliyatini olib boradi.</p>
                                </div>

                                <div id="Functions" ref={tabTwo} class="tabcontent">
                                    <h3>Bizning maqsadimiz</h3>
                                    <p>Asosiy maqsadi: Ilmiy innovatsion ishlanmalarni yaratish, sinovadan otkazish, tijoratlashtirish va ishlab chiqarishga joriy qilishdan  iborat.</p>
                                </div>

                                <div id="Management" ref={tabThree} class="tabcontent">
                                    <h3>Bizning vazifamiz</h3>
                                    <p>TKTI xodimlarining ilmiy-tadqiqot ishlari natijalarini litsenziya shartnomalari tuzib transfer qilish va kichik korxonalar yaratish ishlari boyicha faoliyat olib boradi. Ishlab chiqarishga tayyor ishlanmalarni tijoratlashtirish uchun investorlarni, hamkorlarni jalb qiladi. Ishlab chiqarish korxonalari bilan birgalikda professor oqituvchilar tomonidan tayyorlangan yangi texnologiya hamda yangi maxsulotlar ishlab chiqarish uchun hamkorlikda aloqalar olib boradi. Bitiruvchi va iqtidorli yoshlarni innovatsion ishlanmalar, texnologik loyixalar startaplarga jalb etadi. Ozbekiston Respublikasi Fanlar Akademiyasi ilmiy tadqiqot institutlari, Ozkimyosanoat AJ va Ozqurilishmateriallari AJ va boshqa ishlab chiqarish korxonalari ortasida ozaro manfatli innivatsion xamkorlikni yolga qoyadi. Innovatsion ishlanmalarni patentlashtirish, sertifikatlash ishlarini tizimli ravishda takomillashtirish, bu yonalishda konsalting xizmatlarini joriy etish. Isntitutdagi ilmiy ishlanmalar natijalarini xalqaro miqyosida targib qilishdan iborat.
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-10">
                                <div class="teachers justify-content-between">
                                    <div class="wrapper-personal">
                                        <div class="photo-personal">
                                            <img class="images-personal w-100" src={Image} alt="About us" />
                                            <div class="photo__large">
                                                <div class="bg-text">
                                                    <span class="degree">Bo'lim boshlig'i</span>
                                                    <span class="info">Adilov  Ravshan  Irkinivich

                                                        Ilmiy darajasi: Texnika fanlari nomzodi

                                                        Ilmiy unvoni: Dotsent

                                                        Lavozimi: Ilmiy  innovatsion  ishlanmalarni  tijoratlashtirish  bolimi boshligi</span>
                                                </div>
                                            </div>
                                        </div>
                                        <h6 class="fullname-personal">Adilov  Ravshan  Irkinivich</h6>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="navbar-side">
                    <ul className="nav-ull">
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Bo`limlar</p></li>
                        <li className="nav-ul-li active"><a href="">Ilmiy innovatsion ishlanmalarni tijoratlashtirish bolimi</a></li>
                        <Link to={'/page/static/get/8'}>
                        <li className="nav-ul-li"><a href="">Marketing va talabalar amaliyoti bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/9'}>
                        <li className="nav-ul-li"><a href="">Jismoniy va yuridik shaxslarning murojaatlari bilan ishlash, nazorat va monitoring bo'limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/15'}>
                        <li className="nav-ul-li"><a href="">Xalqaro aloqalar bo`limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/13'}>
                        <li className="nav-ul-li"><a href="">Talim sifatini nazorat qilish bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/10'}>
                        <li className="nav-ul-li"><a href="">Oquv-uslubiy bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/7'}>
                        <li className="nav-ul-li"><a href="">Magistratura bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/6'}>
                        <li className="nav-ul-li"><a href="">Kadrlar bo'limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/12'}>
                        <li className="nav-ul-li"><a href="">Sirtqi bo'lim</a></li>
                        </Link>
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Markazlar</p></li>
                        <Link to={'/page/static/get/11'}>
                        <li className="nav-ul-li"><a href="">Raqamli ta'lim texnologiyalari markazi</a></li>
                        </Link>
                        <Link to={'/page/static/get/14'}>
                        <li className="nav-ul-li"><a href="">TKTI huzuridagi Pedagogik kadrlarni qayta tayyorlash va malakasini oshirish tarmoq markazi</a></li>
                        </Link>
                        <Link to={'/page/static/get/5'}>
                        <li className="nav-ul-li"><a href="">Innovasion markazi</a></li>
                        </Link>
                    </ul>
                    </div>
                    </div>
            </div>
            <Footer />
        </>
    );
}

export default PageStaticOne;
