            p5: "- yoshlarni, xususan uyushmagan yoshlarni manaviy-axloqiy jihatdan va harbiy vatanparvarlik ruhida tarbiyalash, ularda tarixiy xotira, milliy gurur va ozlikni anglash, roy berayotgan voqealarga daxldorlik, milliy va umuminsoniy qadriyatlarga hurmat tuygularini shakllantirish;",
            p6: "- yoshlarning huquq va qonuniy manfaatlarini himoya qilish, ularning zamonaviy kasblarni egallashga bolgan intilishini qollab-quvvatlash, tadbirkorlik faoliyatiga jalb qilish;",
            p7: "- istedodli yoshlarni qollab-quvvatlash, ular oz ijodiy va intellektual salohiyatini royobga chiqarishlari uchun shart-sharoitlar yaratish hamda ilmiy faoliyatga jalb qilish;",
