            thirteenth: "Filialda oquv jarayoni 2019/2020 oquv yilidan boshlangan.",
            fourteenth: "Filialning rasmiy web sayti:",
            yf1: "TKTI Yangiyer filiali",
            yf2: "Ozbekiston Respublikasi Vazirlar Mahkamasining 2019 yil 9 iyuldagi 573-sonli qaroriga asosan Toshkent kimyo-texnologiya instituti Yangier filiali tashkil etilgan.",
            yf3: "Filialda hozirda 2020/2021 oquv yilida jami 56 nafar professor-oqituvchilar faoliyat yuritmoqda, jumladan, 1 nafar akademik, 4 nafar professor, 7 nafar dotsent, 17 nafar katta oqituvchi hamda 27 nafar assitsentlar faoliyat yuritib kelmoqda.",
            yf4: "2020-2021 oquv yili uchun tasdiqlangan namunaviy oquv rejalar asosida oquv jarayonlari tashkil etildi.",
            yf5: "Filialda 6 ta talim yonalishlari boyicha jami 1068 nafar talabalar tahsil olishmoqda. Shundan 1 kurs 747 nafar, 2-kurs 196 nafar, sirtqi talim shaklida 321 nafarni tashkil etadi.",
            yf6: "Oliy va orta maxsus talim vazirligi tomonidan tashkil etilgan elektron talim platformasi orqali oquv mashgulotlari tashkil etilib borilmoqda.",
            yf7: "2020 yil 17 oktabrda Ozbekiston Respublikasi Bosh vaziri orinbosari B.A.Musaevning Oliy va orta maxsus talim vazirligi masullari hamda vazirlik tassarufidagi oliy talim muassasalari rahbarlari bilan masofaviy tarzda bolib otgan videoselektor yigilishida, oliy talim muassasalarida oquv jarayonini ananaviy tarzda davom ettirish boyicha bir qator korsatma va topshiriqlar berilganligini inobatga olgan holda oquv-uslubiy, tashkiliy-metodik jihatdan tayyorgarlik korildi.",
            yf8: "Joriy yil (2021/2022) oquv yili uchun 6 ta yonalish boyicha jami 675 ta shundan, 475 ta kunduzgi 200 ta sirtqini tashkil etmoqda.Kunduzgi 475 ta (shundan davlat granti 85 ta, tolov kontrakt asosida 390 ta);",
            yf9: "Sirtqi 200 ta (tolov kontrakt asosida 200 ta).",
            yf10: "Hozirgi kunda Respublikamizda faoliyat yuritayotgan ishlab chiqarish korxonalarining malakali yetuk kadrlarga bolgan ehtiyojidan hamda talim va ishlab chiqarishning ozaro integratsiyasini taminlash zaruratini inobatga olgan holda filialimiz bilan hamkorlikda Sirdaryo viloyatida faoliyat olib borayotgan korxona va tashkilot rahbarlari bilan hamkorlik ishlar yolga qoyilgan.",
            yf11: "Hamda bir qator ilmiy tadqiqot institutlari ishlab chiqarish korxonalari bilan, shu jumladan.Yangier temir-beton konstruksiyalari zavodi UK, OKMK Sherobod sement zavodi OAJ, .Navoiazot AJ, Metal beton sanoat MChJ, Maksam-Chirchiq AJ, Dehqonobod kaliy zavodi AJ, OzRFA Umumiy va noorganik kimyo instituti bilan ilmiy tadqiqot hamda talabalar amaliyotini tashkil etish boyicha hamkorlik qilish boyicha kelishuvlarga erishildi.",
            yf12: "Mamlakatimizda qishloq xojaligini diversifikatsiya qilish, mahsulot yetishtirishni kopaytirish va eksport qilishga alohida etibor qaratilmoqda. Bu yerning unumdorligini oshirish, mineral ogitlar ishlab chiqarishni talab etmoqda.Shu bois Ozkimyosanoat aksiyadorlik jamiyati tomonidan Samsung Injinering kompaniyasi bilan hamkorlikda ammiak va karbamid zavodi qurish rejalashtirilgan.",
