        otmHaqida: {
            first: "Mustaqillikka tengdosh Toshkent kimyo-texnologiya instituti(TKTI) Ozbekiston Respublikasi Prezidentining 1991 yil 6 maydagi 203-sonli Farmoniga binoan sobiq Toshkent politexnika instituti tarkibidagi kimyoviy texnologiya va muhandislik texnologiyasi fakultetlari negizida tashkil topdi. institut otgan davr ichida kimyo, oziq-ovqat, neft-gaz, qurilish materiallari va iqtisodiyotning boshqa tarmoqlariga 10 mingdan ziyod yuqori malakali muhandis texnologlar, talim yonalishlari boyicha mingdan ortiq bakalavr va 30 ga yaqin mutaxassisliklar boyicha magistrlar tayyorlab berdi. Shuningdek, kafedra va ilmiy- tadqiqot laboratoriyalarida bajarilgan katta hajmdagi izlanishlar natijalariga kora 40 dan ortiq fan doktorlari va 138 nafar fan nomzodlari tayyorlandi. Institutda tahsil olgan mutaxassislar respublikamizning hamda ayrim MDH davlatlarining koplab hukumat tashkilotlarida va ishlab chiqarishning barcha tarmoqlarida masuliyatli lavozimlarda samarali faoliyat korsatib kelyaptilar. Hozirgi kunda TKTI Respublikamiz oliy talim tizimida malakali kadrlar tayyorlab beruvchi yirik tayanch oliy oquv yurtlaridan biri bolib hisoblanadi. Oquv, oquv-uslubiy, ilmiy-tadqiqot, manaviy-marifiy, talim-tarbiya va boshqa ishlarda erishgan yutuqlari tufayli yuqori reyting korsatkichlari bilan oliy oquv yurtlari orasida munosib etakchi orinni egallab turibdi.",
            second: "Institut tarixi Ozbekistonda kimyo, qurilish materiallari, neft-gaz, oziq-ovqat va shu kabi turdosh tarmoqlar sanoat korxonalarining rivojlanishi bilan bevosita bogliqdir.Sobiq Orta Osiyo davlat universitetining kimyo fakultetiga qarashli bolgan kimyo texnologiya bolimida 1934 yili birinchi marotaba Silikatlar texnologiyasi mutaxassisligi boyicha 5 nafar, Yoglar texnologiyasi  mutaxassisligi boyicha 7 nafar, Charm-dubil ekstraktlari texnologiyasi  mutaxassisligi boyicha 3 nafar, 1939 yilgacha 192 nafar injener texnologlar tayyorlandi. Toshkent kimyo-texnologiya instituti tarixi 1940 yilda kimyo texnologiya fakultetini tashkil etish boyicha elon qilingan xukumat qaroridan boshlanadi. Institutda tahsil olganlardan 5 nafari Ozbekiston Respublikasi fanlar akademiyasining akademigidir. Kadrlar tayyorlash dastlab 3 ta mutaxassislikda olib borilgan bolsa, hozirgi kunda 10 ta talim yonalishida bakalavrlar, 27 ta mutaxassislik boyicha raqobatbardosh magistrlar etkazib berilmoqda. Institut tarkibida 5 ta fakultet, 2 ta oquv  uslubiy markaz, 26 kafedra, ixtisoslashtirilgan litsey, 12 ta ilmiy  tadqiqot laboratoriyalari va Kimyogar ilmiy  texnologik parki faoliyat yuritmoqda.",
            third: "Institutda iqtidorli yoshlarni qollab-quvvatlash va ularni ragbatlantirishga alohida etibor qaratilgan. Yuqori korsatkichlar bilan oqiyotgan talabalar Prezident va mashhur olimlarimiz Beruniy, Ulugbek kabi respublika tanlovlarida faol qatnashib kelmoqdalar va sovrinli orinlarni egallamoqdalar. Institutimizda faoliyat korsatgan ulug olimlarimiz, akademiklar Axmedov K.S., Nabiev M.N. Rustamov X.R, professorlar Niyazov M.I.va Markman A.L.nomidagi hamda tarmoq stipendiyalari ham tasis etilgan.",
            fourth: "Institutda tayyorlangan kadrlar OzkimyosanoatDAK, OzneftgazMXK, Ozmevasabzavotuzumsanoat xolding,  Ozyogmoytamakisanoat , Ozdonmahsulot , Ozqurilishmateriallari kompaniyalari va Shortangazkimyo kompleksida etakchi mutaxassis bolib samarali faoliyat korsatmoqdalar."
        },
