            p3: "Oquv muassasasi, tugatgan yili: Toshkent kimyo-texnologiya instituti (bakalavriatura), 2004 yil, . Toshkent kimyo-texnologiya instituti (magistratura), 2006 yil, Ozbekiston Respublikasi Prezidenti huzuridagi davlat boshqaruvi akademiyasi (magistratura), 2018 yil.",
            p4: "Ilmiy darajasi, unvoni (berilgan yili): kimyo  fanlari doktori  (2019 y.), dotsent (2015 y.).",
            p5: "Dissertatsiya himoyasi (mavzusi, ixtisosligi, himoya vaqti, ixtisoslashgan kengash): Polikondensatsion turdagi kationitlar bilan sanoat va oqova suvlarni deminerallashtirish va tozalsh, 11.00.05-Atrof mihitni muhofaza qilish va tabiiy resirslardn oqilona foydalasnish, 2019 yil, O'zbekiston milliy universiteti huzuridagi Ilmiy darajalar beruvchi Ilmiy Kengash",
            p6: "Ish joyi va lavozimi: Toshkent kimyo-texnologiya instituti ilmiy ishlar va innovatsiyalar  boyicha prorektor",
            p7: "Ilmiy-tadqiqot ishlari doirasi: atrof muhitni muhofaza qilish va tabiiy resurslardan oqilona fojdalanish, oqova suvlarni tozalashning samarali texnologiyasini yaratish, mahalliy xomashyolar asosida ion-almashinuvchi polimerlarni sintez qilish, oqova suvlardan ogir metall ionlarini ajratib olish, ekologik talim.",
            p8: "Ilmiy va pedagogik mehnat faoliyati",
            p9: "2000-2004 yy. - Toshkent kimyo-texnologiya instituti talabasi, 2004-2006 yy. - Toshkent kimyo-texnologiya  instituti magistranti",
            p10: "2004-2006 yy. - Toshkent kimyo-texnologiya  instituti sanoat ekologiyasi kafedrasi katta laboranti, 2006-2008 yy. - Toshkent kimyo-texnologiya instituti aspiranti, 2008-2009 yy. - Toshkent kimyo-texnologiya  instituti sanoat ekologiyasi kafedrasi assistenti",
            p11: "2009-2013 yy. - Toshkent kimyo-texnologiya  instituti sanoat ekologiyasi kafedrasi katta oqituvchisi, 2013-2018 yy. - Toshkent kimyo-texnologiya  instituti sanoat ekologiyasi kafedrasi mudiri, 2018 yil 2 avgustidan Toshkent kimyo-texnologiya  instituti yoshlar bilan ishlash boyicha prorektor.",
