            p3: "Bizning vazifamiz ",
            p4: "Kafedra haqida ",
            p5: "Fizika va elektrotexnika kafedrasida bakalavrlar uchun fizika,elektrotexnika va elektronika asoslari va amaliy elektronika fanlari oqitilib kelinmoqda. Kafedrada mexanika va molekulyar fizika, elektr va magnetizm, optika va atom fizikasi bolimlari boyicha hamda elektrotexnika va elektronika asoslari fanlaridan oquv laboratoriya xonalari mavjud bolib, talabalar tajribalar orqali fizika va elektrotexnika va elektronika asoslari fanlarining asosiy qonuniyatlarini organadilar. ",
            p6: "Bizning maqsadimiz ",
            p7: "Kafedrada professor-oqituvchilaridan dots. Xudayberdieva A.I., mustaqil tadqiqotchi sifatida doktorlik (Dr of Sc) dissertatsiyasi ustida tadqiqotlar olib bormoqda. Kafedra oqituvchilaridan  kat. oqit. Ernazarov Sh.N., kat.oqit.Usarov R.R. ass.Eshquvatov X.E. lar mustaqil tadqiqotchi sifatida (Phd) dissertatsiyasi boyicha ilmiy-tadqiqot ishlarini olib bormoqdalar. Kafedrada ilmiy-uslubiy seminar doimiy ravishda faoliyat kosatib kelmoqda. Bu seminarda kafedra professor-oqituvshilari tomonidan bajarilayotgan ilmiy va ilmiy -uslubiy ishlar natijalari, hamda fizikaning dolzarb muammolari boyicha maruzalar muhokama qilinadi. ",
            p8: "Bizning vazifamiz ",
            p9: " Ozbekiston Milliy Universiteti fizika fakultetining Fizikaviy elektronika va Fotonika kafedralari;  Ozbekiston Fanlar Akademiyasi Ion plazmasi va lazer texnologiyalari ilmiy-tekshirish instiuti; Ozbekiston Fanlar Akademiyasi Yadro fizikasi ilmiy-tekshirish instiuti;  Ozbekiston Fanlar Akademiyasi Astranomiya ilmiy-tekshirish instiuti; Toshkent davlat texnika universtiti Umumiy fizika kafedrasi; Namangan muhandislik-qurilish instituti; Rossiya Federatsiyasi Sankt-Peterburg shahridagi ROSATOM texnika Akademiyasi;  Rossiya Federatsiyasi Ural Federal universiteti qoshidagi Radiatsiyaviy sterilizatsiya markazi; Rossiya Federatsiyasi Novosibirsk Davlat Texnika Universiteti Umumiy fizika kafedrasi (Memorandum). ",
