            third: "TASHKENT INSTITUTE OF CHEMISTRY AND TECHNOLOGY BY THE DECREE OF THE PRESIDENT OF UZBEKISTAN ON MAY 6, 1991. ORGANIZED ON THE BASIS OF TASHKENT POLYTECHNICAL INSTITUTE NAMED AFTER BERUNI, FACULTY OF CHEMICAL TECHNOLOGY.",
