            second: "Goals and objectives",
            p1: "The Union of youth of Uzbekistan is a non-profit organization that unites the youth of Uzbekistan in the organizational and legal form of the Union of individuals with the aim of forming a healthy, spiritually mature and developed, independent-thinking young generation, protecting the youth from external threats and harmful effects of mass culture, comprehensively facilitating the protection of the rights and legitimate",
            p2: "The main purpose of the union is to further deepen democratic, political and economic reforms carried out in the country, strengthen peace and harmony in society, involve Uzbekistan in the processes of its accession to the developed countries of the World, ensure effective protection of the rights, freedoms and legitimate interests of the younger generation, raise the spiritual and professional level of young",
            p3: "The main tasks of the Union are as follows.",
            p4: "- formation of young people who are competent, independent-minded, have their own life position and determination, are able to occupy a worthy place in the life of society, to reform the country, to become a decisive force and base in the processes of renewal, to raise their political culture, legal literacy and legal consciousness;",
            p5: "- to educate young people, especially unorganized young people spiritually and morally, in the spirit of military patriotism, historical memory, national pride and self-awareness, belonging to the events taking place, formation of feelings of respect for national and universal values;",
            p6: "- protection of the rights and legitimate interests of young people, support their aspiration for the acquisition of modern professions, involvement in entrepreneurial activity;",
            p7: "- support of talented young people, creation of conditions for their realization of their creative and social potential and involvement in scientific activities;",
            p8: "- formation of a healthy lifestyle, ecological culture among young people, in particular unorganized youth, their wide involvement in regular physical education and sports;",
            p9: "- to protect young people, especially unorganized young people from the influence of religious-extremist and other destructive organizations, from the negative effects and threats that come under the guise of mass culture from abroad, to facilitate the profiling and Prevention of offenses and crime among young people, etc.",
