            first: "Branches of the Tashkent Institute of Chemical Technology",
            second: "Yangier Branch of Tashkent Institute of Chemical Technology",
            third: "Shakhrisabz branch of Tashkent Institute of Chemical Technology",
            fourth: "TCTI Shahrisabz branch",
            fivth: "In order to further increase the potential of the food production industry in the Kashkadarya region and expand the training of personnel with higher education in the basic sectors of the economy, the Ministry of Higher and Secondary Special Education, the Ministry of Economy and Industry, the Ministry of Finance and the Khokimiyat of the Kashkadarya region of the Republic of Uzbekistan have established the Shakhrisabz branch of the Tashkent Institute of Chemical Technology (hereinafter -branch). the proposal to create a fund was agreed.",
            sixth: "The main tasks of the branch are as follows:",
            seventh: "training of qualified personnel possessing practical skills in the field of specialties with high needs, taking into account the development prospects and priority tasks of the chemical and food industry, modern technical and technological trends in the region",
            eighth: "cooperation with leading higher educational institutions and scientific centers in the field of production and processing in order to carry out educational and scientific projects, attract qualified specialists to the process of education and research;",
            nineth: "active development of innovative research aimed at training and retraining qualified personnel in all areas of chemical and food technologies, solving the problems of enterprises;",
            tenth: "to increase the scientific and pedagogical potential of the branch by sending prospective young graduates to master's and doctorate programs to the Republican and foreign higher educational institutions and scientific centers;",
            eleventh: "In order to increase the prestige of the branch in the region and internationally, expand the opportunities for foreign citizens to receive education and apply advanced international experience;",
            twelveth: "introduction and development of webinar teaching methods.",
            thirteenth: "The training process in the Branch began in 2019/2020 academic year.",
            fourteenth: "Official website of the branch:",
            yf1: "TCTI Yangiyer branch",
            yf2: "According to the decision of the Cabinet of Ministers of the Republic of Uzbekistan dated July 9, 2019  573, the Newier branch of the Tashkent chemical and Technological Institute was established.",
            yf3: "Currently, the branch has a total of 56 professors and teachers, including 1 academician, 4 professors, 7 associate professors, 17 senior teachers and 27 assistants.",
            yf4: "Training processes were organized on the basis of standard training plans approved for the 2020-2021 academic year.",
            yf5: "In total, 1068 students are studying in 6 educational areas of the branch. 1 course consists of 747 people, 2-course 196 people, 321 in the form of correspondence education.",
            yf6: "Training sessions are organized through the e-learning platform established by the Ministry of higher and secondary special education.",
            yf7: "On October 17, 2020, Prime Minister of the Republic of Uzbekistan took place.A.At the video-projector meeting held at a distance with the officials of the Ministry of Higher and secondary special education of Musaev, as well as the heads of the higher educational institutions of the Ministry of Education, a number of instructions and instructions were given for the normal continuation of the educational process in higher educational institutions, educational-methodical, organizational-methodological preparation was",
            yf8: "In the current year (2021/2022) for the academic year, a total of 675 pieces of 6 directions of which, 475 pieces of daily 200 correspondence is organized.475 units during the day (of which the state grant is 85 units, payment is 390 units by contract);",
            yf9: "Correspondence 200 PCs (payment is by contract 200 PCs).",
            yf10: "Taking into account the need for qualified personnel of the production enterprises operating in the Republic, as well as the need to ensure mutual integration of education and production, cooperation with the heads of enterprises and organizations operating in the Syrdarya region has been established in cooperation with our branch.",
            yf11: "And a number of scientific research institutes, including with manufacturing enterprises.Yangier reinforced concrete structures plant UK, Okmc Sherabad Cement Plant JSC .Agreements were reached on cooperation with Navoiazot JSC, Metal concrete industry LLC, Maksam-Chirchik JSC, Dekhkanabad potash Plant JSC, Institute of General and inorganic chemistry of Uzrfa  for scientific research and organization of student practice.",
            yf12: "Particular attention is paid to the diversification of Agriculture in our country, the reproduction and export of crop production. This requires increasing the fertility of the Earth, the production of mineral fertilizers.Therefore, it is planned to build a plant of ammonia and urea in cooperation with Samsung Engineering by the Uzkimyosanoat Joint-Stock Company.",
