        // *****************Text-In-Pages EN*******************

        otmHaqida: {
            first: "Tashkent Institute of Chemical Technology was founded in May 1991 on the basis of the Chemical Technology Faculty of the Tashkent Polytechnic Institute. It is the leading chemical and technological university of Uzbekistan. The Institute of Traditional prepares specialists for the chemical, oil refining, biochemical industries, as well as the construction materials industry, porcelainfaience, furniture, woodworking, oilfat, flourmolar, canning, meat and dairy industries.Currently, the institute consists of 4 faculties and a lyceum, where 2, 450 students study.About 300 postgraduates and doctoral students, and candidates of sciences, associate professors, 50 doctors of sciences, professors are engaged in pedagogical and scientific activities. Today, the Institute's research covers almost all branches of chemistry, chemical technology, biotechnology, food technology, as well as non-chemical industries. The main areas of scientific activity are: energy and resource-saving technologies, construction, finishing and decorative materials, composite and structural materials, technology of wood plastics based on thermoplastic polymers, chipboard technology, MDF technology, technology of production of materials for medicine and healthcare, materials for electronics, petrochemistry and oil refining, mineral fertilizers, biotechnology, winemaking, alcohol technology, food preservation technology, meat technology  milk and vegetable oils, rare and dispersed metals methods of cybernetics in chemical technology, chemistry and technology of polymers, plastics, information systems and technologies, integrated environmental studies, varnishes and paints, economics, organization and management of entrepreneurship.",
            second: "The powerful scientific and human potential allowed us to develop unique technologies, create new materials used in the field of chemistry, energy, construction, medicine, computer science, metallurgy, transport and ecology, monomers, polymers, new materials for medicine, construction, information services, devices and control systems, etc. Scientists of the Institute have received several dozen patents of the Republic of Uzbekistan over the past 5 years, published more than 1, 000 scientific articles, collections and monographs.More than 46 projects are carried out according to the scientific and technical programs of the Center for Science and Technology under the Cabinet of Ministers of the Republic of Uzbekistan. The Institute is the head within the framework of a major scientific and technical program. Scientific research of higher education in priority areas of science and technology.",
            third: "Scientists of the Tashkent Institute of Chemical Technology have developed medicines, technology for processing phosphogypsum  waste of phosphoric acid production, corrosionresistant non-metallic materials, corrosion inhibitors, materials based on special types of glass ceramics, binders, metals for use in bone endocrinology in traumatology, facial surgery and dentistry; flame retardant materials; information and software products, etc. Much attention is paid to ecology at the Institute, TCHTI takes part in the implementation of the republican target program Aral",
            fourth: "The Institute successfully operates a methodological council for the sustainable development of interdisciplinary approaches to the development of curricula and programs, training in decision-making techniques that take into account the short- and long-term consequences of these decisions for the economy and the environment. The Institute's research is being developed in cooperation with scientists from Russia, South Korea, Japan, Turkey, Germany and other countries."
        },
