            a3: 'The faculty cooperates with all production enterprises under JSC" Uzsharobsanoat". At the faculty, students qualification practices are conducted directly in production enterprises.In particular, the zangiota winemaking unitary experimental enterprise and the branch of enology and Technology of Bijgish products and soft drinks were established in the Tashkent wine axioneering society.In these branches there is training of specialty subjects.',
