            p5: "The Department of Physics and Electrical Engineering teaches the basics of physics, electrical engineering and electronics and applied electronics for bachelors. The department has laboratories in the departments of mechanics and molecular physics, electricity and magnetism, optics and atomic physics, as well as in the basics of electrical engineering and electronics. rganadilar. ",
