            p8: "Our mission ",
            p9: " Departments of 'Physical Electronics' and 'Photonics' of the Faculty of Physics of the National University of Uzbekistan; Research Institute of Ion Plasma and Laser Technologies of the Academy of Sciences of Uzbekistan; Research Institute of Nuclear Physics of the Academy of Sciences of Uzbekistan; Astronomy Research Institute of the Academy of Sciences of Uzbekistan; Tashkent State Technical University, Department of General Physics; Namangan Institute of Civil Engineering; ROSATOM Technical Academy in St. Petersburg, Russian Federation; Radiation Sterilization Center at the Ural Federal University of the Russian Federation; Novosibirsk State Technical University, Russian Federation, Department of General Physics (Memorandum). ",
