            p7: "To date, professors and teachers of the department of 'Physical Culture and Sports' have organized dozens of scientific articles in the field of physical education, scientific-practical conferences and theses in scientific collections, monographs, scientific-methodical manuals, physical education classes. In order to improve the quality of training of highly qualified personnel, recommendations are being developed to provide them to higher education institutions for use in the educational process.",
