import Header from '../../components/header/header'
import Footer from '../../components/footer/footer'
import { useRef } from 'react'
import { useContext } from 'react';
import context from '../../lang/lang';
import { Context as LanguageContext } from '../../Context/Language';
import ParticlesBg from 'particles-bg'
import Image1 from '../../images/kafedra/noorganik.jpg'
import Image00 from '../../images/kafedra/noorg-xaqida.jpg'
import { Link } from 'react-router-dom';
import Logo from '../../images/logo-circle.png'




function PageStaticOne() {
    const tabOne = useRef()
    const tabTwo = useRef()
    const tabThree = useRef()
    const { lang } = useContext(LanguageContext);
    return (
        <>
            <ParticlesBg color="#307ec7" type="cobweb" num={50} bg={true} />
            <div className="container-pages">
                <Header />
                <div className="mainsss">
                <div className='page_main'>
                    <div class="row">
                        <div class="col-lg-10 col-md-12">
                            <h3>{context[lang].kafedra.sixteenth}</h3>
                            <div class="tab">
                                <button class="tablinks" onClick={() => {
                                    tabTwo.current.classList.remove("opened")
                                    tabThree.current.classList.remove("opened")
                                    tabOne.current.classList.add("opened")
                                }} id="defaultOpen">Kafedra haqida
                                </button>
                                <button class="tablinks" onClick={() => {
                                    tabOne.current.classList.remove("opened")
                                    tabThree.current.classList.remove("opened")
                                    tabTwo.current.classList.add("opened")
                                }} id="defaultOpen">Kafedra tarixi</button>
                                <button class="tablinks" onClick={() => {
                                    tabOne.current.classList.remove("opened")
                                    tabTwo.current.classList.remove("opened")
                                    tabThree.current.classList.add("opened")
                                }} id="defaultOpen">Ilmiy yonalishlar</button>
                            </div>
                            <div id="About" ref={tabOne} class="tabcontent opened">
                                <h3>Kafedra haqida</h3>
                                <p>Toshkent kimyo-texnologiya institutining yetakchi kafedralaridan xisoblanadigan Noorganik moddalar kimyoviy texnologiyasi kafedrasi juda qadim va boy tarixga ega. Butunittifoq oliy talim Qomitasi raisining 1937 yildagi qaroriga binoan Orta Osiyo Davlat universitetining kimyo fakulteti tarkibida 1939 yili Noorganik moddalar texnologiyasi kafedrasi tashkil etildi. Osha davrda Orta Osiyo Davlat universitetining rektori bolib ishlayotgan M.S. Elgortning shogirdi dotsent S.A. Sigov mudirlik qila boshladi. Shu yili kafedrada noorganik moddalar texnologiyasi yonalishida 13 ta mutaxassis kimyogar-muxandislar tayyorlandi va ular Ozbekiston Respublikasining turli kimyo korxonalariga yollanma oldilar.</p>
                                <p> 1940 yilning yozida kimyo fakulteti tarkibida kafedralar, shu jumladan Noorganik moddalar texnologiyasi kafedrasi bilan birgalikda Orta Osiyo Davlat Universitetida Orta Osiyo sanoat instituti tassarufiga otkazildi.Lekin kafedra xodimlari va talabalari shu yil boshlangan urush tufayli oquv yilini oxirlay olmadilar. Ulug Vatan urushi yillarida kafedra mudiri S.A. Sigov va assistent Z.M. Leykin Vatan himoyasiga safarbar etilganliklari tufayli kafedraga qisqa vaqt kimyo fanlari nomzodi, dos. A.B.Kuchqarov va OzR Davlat rejalashtirish Qomitasi raisining birinchi orinbosari M.N.Nabievlar mudirlik qilganlar.</p>
                                <p>Urishdan qaytganidan keyin 1946-1967 yilgacha kafedra mudirligi faoliyatini dos. S.A.Sigov davom ettirgan, 1968 yildan 1990 yilgacha texnika fanlari doktori, professor F.M.Mirzaev mudirlik qilgan davrlarda OzRFA kimyo institutidagi ogitlar kimyosi va texnologiyasi bolimi bilan yaqin ilmiy hamkorlik yolga qoyildi, ilm Fan gurkirab rivojlandi va buning oqibatida noorganik moddalar, shu jumladan mineral va organomineral ogitlar kimyosi va texnologiyasi borasida olib boriladigan keng qamrovli ilmiy-tadqiqot ishlari natijalari boyicha Respublikaning kimyo korxonalariga amaliy tavsiyalar berildi va ular Qoqon va Samarqand superfosfat korxonalari, Navoiy va Olmaliq kimyo korxonalari, Fargona azotli ogitlar ishlab chiqarish korxonasida joriy etildi.</p>
                            </div>

                            <div id="Functions" ref={tabTwo} class="tabcontent">
                                <h3>Kafedra tarixi</h3>
                                <p>  Kafedraga 1990-1992 yillarda texnika fanlari nomzodi, dotsent Sh.A.Yakubov, 1992-1994  yillarda texnika fanlari doktori P.X. Mansurov, 1994-2000 yillarda texnika fanlari doktori professor T.A.Atakuziev, 2000-2001 yy., 2006-2009 yillarda t.f.n., dotsent Z.K.Toirov, 2001-2005 yy, 2009-2011 yillarda texnika fanlari doktori  prof. A.U.Erkaev, 2005-2006 yilarda t.f.d. Iskandarova M.I. mudirlik qilganlar. 2011-2015 yillarda texnika fanlari nomzodi, dotsent Iskenderov A.M., 2016 yilning yanvar oyidan boshlab hozirgi kungacha texnika fanlari nomzodi, dotsent Adilova M.Sh. kafedraga rahbarlik qilib kelmoqda.</p>
                                <p>Kafedra xodimlari oz faoliyati davrida shu kunga qadar Respublikamizning kimyo korxonalari, Oliy talim va ilmiy tadqiqot hamda loyihalash institutlari uchun 3500 dan ziyod muxandis-texnolog tayyorlab mamlakatimiz iqtisodi yuksalishiga oz munosib xissalarini qoshib kelmoqdalar. Kafedra bitiruvchilarining kopchiligi bugungi kunda tarmoq korxonalari, ilmiy tekshirish institutlari va Davlat rahbarlik organlarida masul lavozimlarda faoliyat korsatmoqdalar. Shu jumladan, Ozbekiston Respublikasi Vazirlar Maxkamasining bosh vazir orinbosari texnika fanlari nomzodi G.I.Ibragimov, texnika fanlari doktori, professor R.Ya.Yakubov Ozkimyosanoat-DAKning boshqaruv raisi muovini lavozimida, erishgan yutuqlari bilan nafaqat respublika, balki chet ellarda ham tanilgan. Ozbekiston Respublikasi FA Umumiy va noorganik kimyo institutining yetakchi olimlaridan OzR FA akademigi S.Toxtaev, texnika fanlari doktori, professor Sh.S.Nomozovlar, Maksam-Chirchiq AJ boshqaruv raisi muovini, texnika fanlari doktori professor A.D.Dadaxodjaevlar ham ishlamoqdalar.</p>
                                <p>Kafedrada yuqori malakali professor-oqituvchilar va ilmiy xodimlar prof. Mirzaev F.M., prof. Mansurov P.X., dos. Yakubov Sh.A., dos. Linkevich V.A., dos. Mannanova R.A. , k.oq. Terexin Ye.L., dos. Mirzaev I., dos. Toirov Z.K., los. Sharapov A.T., ilmiy xodim Sayfullin R.Z. faoliyat korsatib kelgan.</p>
                            </div>

                            <div id="Management" ref={tabThree} class="tabcontent">
                                <h3>Ilmiy yonalishlar</h3>
                                <p>1. Ilmiy-tadqiqot faoliyatining ustuvor yonalishlari. Kafedrada olib borilayotgan ilmiy-tadqiqot ishlari bir qator noorganik maxsulotlar ishlab chiqarish muammolarini xal qilishga bagishlangan bolib, ular asosan, maxaliy xomashyolar asosida mineral ogitlar va tuzlarning yangi turlarini, soda maxsulotlarini, sintetik yuvish vositalarini ishlab chiqarishdan, xamda kimyo sanoatining chiqindilarini utilizatsiya qilishdan iborat.</p>
                                <p>2. Iqtisodiyotning tegishli soxalariga oid korxona va tashkilotlar bilan xamkorlikda bajarilayotgan ilmiy ishlarni natijalariga kora kafedra yuqori ilmiy potensialga egadir. Kafedrada 4 ta fan doktori va 7 ta fan nomzodlari faoliyat korsatmoqdalar.</p>
                                <p>OzR FA umumiy va noorganik kimyo institutidan kafedrani ilmiy-pedagogik faoliyatiga orindosh sifatida 2 ta akademik (Tuxtaev S., Beglov B.M.), fan doktorlari (Nomozov Sh.S., Reymov A.M.) va fan nomzodlari (Kucharov B.X., Usanboev N.X., Alimov U.) jalb etilgan.</p>
                                <p>2014 yilda kafedra jamoasi davlat ilmiy grantlari boyicha 200mln.sumdan oshiq mablagga ilmiy-tadqiqot ishlarini bajargan. 2015 yilda esa kafedra olimlari 210 mln.sum xajmdagi ilmiy davlat grantlarini yutganlar.</p>
                                <p>Qoyidagi bir qator ishlab chiqarish korxonalari bilan 100 mln.sumdan oshiq xajmda xojalik shartnomalar asosida ilmiy-tadqiqot ishlari bajarib kelinmoqda: Navoiyazot, Samarqandkimyo, AJ lari, Dehqonobod kaliyli ogitlar zavodi, Qoqond superfosfat zavodi. Barcha bajaralayotgan ilmiy ishlarning mavzulari Uzkimyosanoat DAK bilan kelishilgan holda amalga oshmoqda.</p>
                                <p>Ilmiy-tadqiqot ishlar bajarilishi natijasida yangi fosforli, murakkab va suyuq ogitlar olishning texnologiyalari ishlab chiqildi, hamda kalsinatsiyalangan soda, oddiy superfosfat va sintetik yuvish moddalar ishlab chiqish texnologiyalari jadallashtirildi.</p>
                                <p>3. Nomzodlik va doktorlik dissertatsiyalari himoyasi. 2009 yili dotsent X.Ch. Mirzakulov doktorlik dissertatsiyasini himoya qildi va xozirgi kunda u fan doktori, professor sifatida kafedrada faoliyatini olib bormoqda. X.Ch. Mirzakulov muallifligida yangi ogitlar olish texnologiyalari Ammofos-Maksam va Samarqandkimyo AJ larida joriy qilingan.</p>
                                <p>Oxirgi uch yil mobaynida kafedrada 6 ta nomzodlik dissertatsiya yoqlandi. N.V. Volnskova va O.K. Rambergenov nomzodlik dissertatsiyalarini natijalari Ammofos-Maksam AJ va Qongirot soda zavodi UK larida joriy etilgan.</p>
                                <p>4. Iqtidorli talabalar bilan ishlash. Kafedra 2-4 kurs bir qator bakalavriat talabalari va 1-2 kurs magistrantlari orasida bir qator talabalari kafedrada tashkil etilgan Texnolog togaragiga azo bolishgan. Ularga ilmiy mavzular va ilmiy rahbarlar tayinlanib, kafedradagi laboratoriyalarda oz ilmiy ishlarini olib borishmoqda. Olib borgan ilmiy-tadqiqot ishlari yuzasidan bir qator ilmiy-texnikaviy anjumanlarda faol qatnashib, maqolalari toplamlarda chop etildi.</p>
                                <p>Bundan tashqari kafedraning 20 ta talabasi ilmiy grantlar va xojalik shartnoma asosidagi ilmiy-tadqiqot ishlariga qatnashib kelmoqdalar</p>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-10">
                            <div class="teachers">
                                <div class="wrapper-personal">
                                    <div class="photo-personal">
                                        <img class="images-personal" src={Image1} alt="About us" />
                                        <div class="photo__large">
                                            <div class="bg-text">
                                                <span class="degree">Kafedra boshlig'i</span>
                                                <span class="info">
                                                    falsafa doktori (PhD), dotsent <br />
                                                    Qabul kunlari: Dushanba, Chorshanba, Juma 900-1200 <br />
                                                    E-mail: djandullaeva@mail.ru <br />
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                    <h6 class="fullname-personal">Djandullaeva Munavara Saparbaevna</h6>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="navbar-side">
                    <ul className="nav-ull">
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Kafedralar</p></li>
                        <li className="nav-ul-li active"><a href="">Noorganik moddalar kimyoviy texnologiyasi kafedrasi</a></li>
                        <Link to={'/page/static/get/18'}>
                        <li className="nav-ul-li"><a href="">Ijtimoiy-siyosiy fanlar kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/19'}>
                        <li className="nav-ul-li"><a href="">Fizika va elektrotexnika kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/20'}>
                        <li className="nav-ul-li"><a href="">Biotexnologiya kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/21'}>
                        <li className="nav-ul-li"><a href="">Jismoniy tarbiya va sport kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/22'}>
                        <li className="nav-ul-li"><a href="">Informatika, avtomatlashtirish va boshqaruv kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/23'}>
                        <li className="nav-ul-li"><a href="">Oziq-ovqat sanoati mashina va jihozlari - Mexanika asoslari kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/24'}>
                        <li className="nav-ul-li"><a href="">Gosht-sut va konserva mahsulotlar texnologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/25'}>
                        <li className="nav-ul-li"><a href="">Oziq-ovqat mahsulotlari texnologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/26'}>
                        <li className="nav-ul-li"><a href="">Organik kimyo va ogir organik sintez texnologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/27'}>
                        <li className="nav-ul-li"><a href="">Sellyuloza va yog`ochsozlik texnologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/28'}>
                        <li className="nav-ul-li"><a href="">Yuqori molekulali birikmalar va plastmassalar kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/29'}>
                        <li className="nav-ul-li"><a href="">Neft gazni qayta ishlash kimyoviy texnologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/30'}>
                        <li className="nav-ul-li"><a href="">Oliy matematika kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/31'}>
                        <li className="nav-ul-li"><a href="">Silikat materiallar, nodir va kamyob metallar texnologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/32'}>
                        <li className="nav-ul-li"><a href="">Analitik, fizikaviy va kolloid kimyo kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/34'}>
                        <li className="nav-ul-li"><a href="">Kimyoviy texnologik jarayon va qurilmalar kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/35'}>
                        <li className="nav-ul-li"><a href="">Sanoat ekologiyasi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/36'}>
                        <li className="nav-ul-li"><a href="">Umumiy va noorganik kimyo kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/37'}>
                        <li className="nav-ul-li"><a href="">Snt iqtisdiyoti v mnjmnti kfdrsi</a></li>
                        </Link>
                        <Link to={'/page/static/get/38'}>
                        <li className="nav-ul-li"><a href="">Mahsulot sifati menejmenti kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/39'}>
                        <li className="nav-ul-li"><a href="">Kasb talimi kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/40'}>
                        <li className="nav-ul-li"><a href="">Tillar kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/41'}>
                        <li className="nav-ul-li"><a href="">Xorijiy tillar kafedrasi</a></li>
                        </Link>
                        <Link to={'/page/static/get/42'}>
                        <li className="nav-ul-li"><a href="">Enologiya va umumiy ovqatlanishni tashkil etish kafedrasi</a></li>
                        </Link>
                    </ul>
                    </div>
                    </div>
            </div>
            <Footer />
        </>
    );
}

export default PageStaticOne;
