import Header from '../../components/header/header'
import Footer from '../../components/footer/footer'
import { useRef } from 'react'
import { useContext } from 'react';
import context from '../../lang/lang';
import { Context as LanguageContext } from '../../Context/Language';
import ParticlesBg from 'particles-bg'
import Image from '../../images/356a192b79_1617447562.jpg'
import Image2 from '../../images/da4b9237ba_1617447572.jpg'
import Image3 from '../../images/77de68daec_1617447579.jpg'
import { Link } from 'react-router-dom';
import Logo from '../../images/logo-circle.png'


function PageStaticOne() {
    const tabOne = useRef()
    const tabTwo = useRef()
    const tabThree = useRef()
    const { lang } = useContext(LanguageContext);
    return (
        <>
            <ParticlesBg color="#307ec7" type="cobweb" num={50} bg={true} />
            <div className="container-pages">
                <Header />
                <div className="mainsss">
                <div className='page_main'>
                    <div class="row">
                        <div class="col-lg-10 col-md-12">
                            <h3>{context[lang].centre.thirteenth}</h3>
                            <div class="tab">
                                <button class="tablinks" onClick={() => {
                                    tabTwo.current.classList.remove("opened")
                                    tabThree.current.classList.remove("opened")
                                    tabOne.current.classList.add("opened")
                                }} id="defaultOpen">Bo'lim haqida</button>
                                <button class="tablinks" onClick={() => {
                                    tabOne.current.classList.remove("opened")
                                    tabThree.current.classList.remove("opened")
                                    tabTwo.current.classList.add("opened")
                                }} id="defaultOpen">Bizning
                                    maqsadimiz</button>
                                <button class="tablinks" onClick={() => {
                                    tabOne.current.classList.remove("opened")
                                    tabTwo.current.classList.remove("opened")
                                    tabThree.current.classList.add("opened")
                                }} id="defaultOpen">Bizning
                                    vazifamiz</button>
                            </div>
                            <div id="About" ref={tabOne} class="tabcontent opened">
                                <h3>Bo'lim haqida</h3>
                                <p>Toshkent kimyo-texnologiya instituti huzuridagi Pedagog kadrlarni qayta tayyorlash va ularning malakasini oshirish tarmoq markazi dastlab Ozbekiston Respublikasi Oliy va orta maxsus talim vazirligining 1999 yil 19 fevraldagi 57-sonli buyrugiga asosan Malaka oshirish fakulteti sifatida tashkil etilgan. Fakultet tomonidan 2012 yilgacha respublikamizdagi oliy talim muassasalarining mutaxassislik fanlari oqituvchilari oz bilim va konikmalarini oshirishlari uchun malaka oshirish kurslari tashkil etilgan bolsa, 2012 yildan Ozbekiston Respublikasi Prezidentining 2012 yil 28 maydagi Malakali pedagog kadrlar tayyorlash hamda orta maxsus, kasb-hunar talimi muassasalarini shunday kadrlar bilan taminlash tizimini yanada takomillashtirishga oid chora-tadbirlar togrisidagi PQ-1761-sonli qarori asosida fakultetda orta maxsus, kasb-hunar talimi tizimi pedagog kadrlari malakasini oshirish kurslari ham yolga qoyilgan. 2015 yildan boshlab esa Ozbekiston Respublikasi Prezidentining 2015 yil 12-iyundagi Oliy talim muassasalarining rahbar va pedagog kadrlarini qayta tayyorlash va ularning malakasini oshirish tizimini yanada takomillashtirish chora-tadbirlari togrisidagi PF-4732sonli farmoniga muvofiq, Vazirlar Mahkamasining 2015 yil 20-avgustdagi Oliy talim muassasalarining rahbar va pedagog kadrlarini qayta tayyorlash va malakasini oshirishni tashkil etish chora-tadbirlari togrisidagi 242sonli qarori, Ozbekiston Respublikasi Vazirlar Mahkamasining 2014 yil 27-martdagi Davlat va xojalik boshqaruvi, mahalliy davlat hokimiyati organlari xodimlarini axborotkommunikatsiya texnologiyalari sohasida malakalarini oshirish boyicha qoshimcha chora-tadbirlar togrisidagi 73-sonli qarori hamda Vazirlar Mahkamasining 2015 yil 21-sentyabrdagi Oliy talim muassasalari pedagog kadrlarini qayta tayyorlash va ularning malakasini oshirish tizimini yanada takomillashtirish chora-tadbirlari togrisidagi 273-sonli qarori asosida Pedagog kadrlarni qayta tayyorlash va ularning malakasini oshirish tarmoq markazi sifatida oz faoliyatini davom ettirib kelmoqda. Tarmoq markazida Qayta tayyorlash va malaka oshirish talim jarayonini tashkil etish va sifatini organish bolimi, Qayta tayyorlash va malaka oshirish talim dasturlari va ilgor tajribani joriy etish bolimi, Xalqaro hamkorlik bolimi mavjud.</p>
                            </div>

                            <div id="Functions" ref={tabTwo} class="tabcontent">
                                <h3>Bizning maqsadimiz</h3>
                                <p>Ozbekiston Respublikasi Prezidentining 2019 yil 27 avgustdagi PF-5789-sonli qaroriga muvofiq oliy talim muassasalari pedagog kadrlarining uzluksiz malakasini oshirish tizimini joriy etish maqsadida tarmoq markazida kunduzgi (bevosita) oqitish bilan bir qatorda masofaviy oqitish tizimi ham tashkil etilgan. Pedagog kadrlarni qayta tayyorlash va ularning malakasini oshirish tarmoq markazida oqitish jarayonini yanada takomillashtirish va sifatini oshirish maqsadida oliy talim muassasalari professor-oqituvchilari uchun quyidagi 5 ta yonalish boyicha qayta tayyorlash va malaka oshirish kurslari tashkil etilgan</p>
                            </div>


                            <div id="Management" ref={tabThree} class="tabcontent">
                                <h3>Bizning vazifamiz</h3>
                                <p>Shuningdek, tarmoq markazi tinglovchilarining amaliy konikmalarini oshirish maqsadida zamonaviy moddiy-texnika bazasi va yuqori texnologiyalarga ega korxona va tashkilotlarda kochma va amaliy mashgulotlari tashkil etilmoqda. Hozirda tarmoq markazi respublikamizning 30 dan ortiq yirik ishlab chiqarish korxona va tashkilotlari, vazirlik hamda qomitalar, Ozbekiston Respublikasi Fanlar kademiyasi huzuridagi ilmiy tadqiqot institutlari hamda xorijning yetakchi universitetlari bilan shartnomalar imzolagan.
                                    <br />
                                    Shuningdek, markaz tinglovchilarining xorijiy professor-oqituvchilar bilan xorijiy tajriba va fikr almashishlari uchun har yili markazga xorijiy davlat universitetlari hamda xalqaro uyushmalar va agentliklardan, jumladan, QShning Chikago Ekolab laboratoriyasi, Buyuk Britaniyaning Xalqaro oziq-ovqat xavfsizligi agentligi, Janubiy Koreyaning Ulsan texnoparki, Belarussiyaning Mogilyov davlat oziq-ovqat universiteti, Rossiyaning Tambov davlat universiteti kabilarning yetakchi professor-oqituvchilar va xalqaro ekspertlari jalb etib kelinmoqda.
                                    <br />
                                    2019 yil davomida Janubiy Koreya, Daniya, QSh, Belarussiya, Xitoy xalq Respublikasi, Rossiya Federatsiyasi va Ukrainadan jami 12 nafar xorijiy mutaxassislar taklif etilgan. 2020 yil davomida Janubiy Koreya, Belarussiya, QSh, Rossiya Federatsiyasi, Buyuk Britaniyadan 22 nafar xorijiy mutaxassislar jalb etilgan. 2021 yilda esa jami 14 nafar yetakchi mutaxassislar tarmoq markazi tinglovchilariga oz tajribalarini almashishlari rejalashtirilgan.
                                </p>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-10">
                            <div class="teachers justify-content-center">
                                <div class="wrapper-personal">
                                    <div class="photo-personal">
                                        <img class="images-personal" src={Image} alt="About us" />
                                        <div class="photo__large">
                                            <div class="bg-text">
                                                <span class="degree">Bo'lim boshlig'i</span>
                                                <span class="info">Mirzaqulov Xoltura Chorievich

                                                    loqalar: Tel.: +998712447928

                                                    E-mail: tkti.tm@mail.ru</span>
                                            </div>
                                        </div>
                                    </div>
                                    <h6 class="fullname-personal">Mirzaqulov Xoltura Chorievich</h6>
                                </div>
                                <div class="wrapper-personal">
                                    <div class="photo-personal">
                                        <img class="images-personal" src={Image2} alt="About us" />
                                        <div class="photo__large">
                                            <div class="bg-text">
                                                <span class="degree">Bosh hisobchi</span>
                                                <span class="info">Rabbimov zam Suratovich
                                                    <br />
                                                    loqalar: Tel.: +998712447928
                                                    <br />
                                                    E-mail: tkti.tm@mail.ru.</span>
                                            </div>
                                        </div>
                                    </div>
                                    <h6 class="fullname-personal">Rabbimov zam Suratovich</h6>
                                </div>
                                <div class="wrapper-personal">
                                    <div class="photo-personal">
                                        <img class="images-personal" src={Image3} alt="About us" />
                                        <div class="photo__large">
                                            <div class="bg-text">
                                                <span class="degree">Mutaxassisi</span>
                                                <span class="info">shurova Shahina Burxonjon qizi
                                                    <br />
                                                    loqalar: Tel.: +998712447928
                                                    <br />
                                                    E-mail: tkti.tm@mail.ru</span>
                                            </div>
                                        </div>
                                    </div>
                                    <h6 class="fullname-personal">shurova Shahina Burxonjon qizi</h6>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="navbar-side">
                    <ul className="nav-ull">
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Bo`limlar</p></li>
                        <li className="nav-ul-li"><a href="">Sirtqi bo'lim</a></li>
                        <Link to={'/page/static/get/8'}>
                        <li className="nav-ul-li"><a href="">Marketing va talabalar amaliyoti bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/9'}>
                        <li className="nav-ul-li"><a href="">Jismoniy va yuridik shaxslarning murojaatlari bilan ishlash, nazorat va monitoring bo'limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/15'}>
                        <li className="nav-ul-li"><a href="">Xalqaro aloqalar bo`limi</a></li>
                        </Link>
                        <Link to={'/page/static/get/13'}>
                        <li className="nav-ul-li"><a href="">Talim sifatini nazorat qilish bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/10'}>
                        <li className="nav-ul-li"><a href="">Oquv-uslubiy bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/7'}>
                        <li className="nav-ul-li"><a href="">Magistratura bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/4'}>
                        <li className="nav-ul-li"><a href="">Ilmiy innovatsion ishlanmalarni tijoratlashtirish bolimi</a></li>
                        </Link>
                        <Link to={'/page/static/get/6'}>
                        <li className="nav-ul-li"><a href="">Kadrlar bo`limi</a></li>
                        </Link>
                        <li className='nav-ull-header'><p> <img src={`${Logo}`} alt="" /> Markazlar</p></li>
                        <Link to={'/page/static/get/11'}>
                        <li className="nav-ul-li active"><a href="">TKTI huzuridagi Pedagogik kadrlarni qayta tayyorlash va malakasini oshirish tarmoq markazi</a></li>
                        </Link>
                        <Link to={'/page/static/get/11'}>
                        <li className="nav-ul-li"><a href="">Raqamli ta'lim texnologiyalari markazi</a></li>
                        </Link>
                        <Link to={'/page/static/get/5'}>
                        <li className="nav-ul-li"><a href="">Innovasion markazi</a></li>
                        </Link>
                    </ul>
                    </div>
                    </div>
            </div>
            <Footer />
        </>
    );
}

export default PageStaticOne;
